@media screen and (max-width: 1276px){
	.wwa{
		flex-direction: column;
		width: 100%;
		font-size: small;
		background: none;
		background-color: #E5E5E5;
	}
	.img{
		display: none;
	}
	.container{
		width:100%;
	}
	.icons{
		padding-top: 30px;
	}
	.photo{
		margin: 0;
	}
	.espace{
		display: none;
	}
	.quemSomos{
		max-width: 800px;
	}
	.icons{
		grid-gap: 10px;
	}
	.text{
		width: 80%;
    font-size: 1.7vh;
	}
	.subTitle{
		font-size: 2vw;
	}
	.quemSomos h1{
		font-size: 5vh;
	}
	.icon{
		width: 20vw;
    height: 10vh;
	}
	/*
	.subTitle{
		font-size: 1vw;
	}
	*/
}

