body, html {
  margin: 0;
}

.post-list {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  width: 100%;
  background-color: rgb(213, 211, 221);
}

.post-card {
  width: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  transition: box-shadow .3s ease-in-out;
  margin: 15px 0;
  cursor: pointer;
}

.post-card:hover {
  box-shadow: 0px 10px 20px #838383;
}

.post-card img {
  width: 100%;
}

.post-card p {
  margin: 0;
  padding: 10px 0;
  width: 100%;
  text-align: center;
  background-color: white;
  font-family: Nunito, sans-serif;
  letter-spacing: 5px;
  text-transform: uppercase;
}

.carouselBlog .slick-slider{
	max-width: 70vw;
  align-items: center;
	left: 17%;
	display: block;
	position: relative;
}
.carouselBlog .slick-slide {
  /*text-align: center;*/
	position: relative;
}

.carouselBlog .slick-prev:before,
.slick-next:before {
  color: #777777;
}


.carouselBlog .slick-prev{
	position: absolute;
	display: flex;
	flex-direction: row;
	left: 33vw;
	z-index: 10;
	
}

.carouselBlog .slick-prev:before{
	content: url(/static/media/prev.3ceed751.svg);
	
}

.carouselBlog .slick-next:before{
	content: url(/static/media/next.a1654069.svg);
	
}

.carouselBlog .slick-next{
	position: absolute;
	display: flex;
	flex-direction: row;
	right: 43vw;
	z-index: 10;
}

/*
.slick-arrow{
	width: 10px;
	border-radius: 100px;
	top: 30vh;
}
*/

.carouselBlog .slick-prev, .slick-next{
	
	height: 0px;
	top: 100%
}

.carouselBlog .slick-dots{
	display: flex;
  align-items: center;
  justify-content: center;
  top: auto;
	padding: 0;
	right: 5vw;
}


.carouselBlog .slick-dots li button:before {
	content: url(/static/media/RectangleSem.eeb30acc.svg);
}

.carouselBlog .slick-dots li.slick-active button:before{
	content: url(/static/media/RectangleCom.7b3e1c25.svg);
}

