  height: 15vh; 
}

.info_client {
  display: flex;
  flex-direction: row;
  margin-left: 7vw;
  margin-right: 7vw;
}

.info_client input {
  margin-left: 0;
  margin-right: 0;
  /* background-color: purple; */
}

.label-float .email_phone {
  margin: 0;
  /* background-color: blue; */
}

.label-float .emailActive {
  margin: 0;
  transform: translate(0, 3vh) scale(1);
  font-weight: 600;
}

.label-float .phoneActive {
  margin: 0;
  transform: translate(0, 3vh) scale(1);
  font-weight: 600;
}

.info_client .label-float { 
  display: flex;  
  width: 65%;
  /* background-color: red; */
}

.info_client .label-float+.label-float {
  margin-left: 5%;  
  width: 30%;
  /* background-color: gray;   */
}

.image_delta {
  margin-top: 8vh;
  display: flex;
  flex-direction: column;
}

.image_delta img {
  height: 80vh;
}

.button_ontact {
  align-self: flex-end;
  position: absolute;
  bottom: 5vh;
  height: 5vh;
  width: 10vw;
  margin-right: 7vw;
  background-color: #032C66;
  color: #ebecee;
  font-family: Poppins;
  font-style: normal;
  font-weight: 600;
  font-size: 95%;
  border: none;
  transition: .5s;
}

.info button:hover{
  background-color: #0850b4;
}

@media (max-width: 1280px){
  .info {
    margin-top: 1.5vh;
    flex-direction: column;
    background-color: #032C66;
    height: 90vh;
    width: 90vw;
    align-items: center;  
  }

  .info_contact {
    margin: 0;
    color: #ebecee;
  }

  .info p {
    color: #ebecee;
    margin-left: 0;
    margin-right: 0;
    margin-top: 4vh;
  }

  .label-float input {
    margin: 2vh 0 0 0;
    color: #ebecee;
    width: 100%;
    border-bottom: 0.22vh solid #ebecee;
    margin-top: 6vh;
    font-size: 2vh;
  }

  .info_client {
    flex-direction: column;      
    width: 100%;    
  }

  .info_client .label-float{
    width: 100%;
  }

  .info_client .label-float+.label-float {
    margin-left: 0;
    width: 100%; 
  }

  .label-float label{
    color: #ebecee;
    margin-left: 0;
  }

  .button_ontact {
    background-color: #ebecee;
    align-self: center;
    color: #032C66;
    margin: 0;
    bottom: 6vh;
    height: 6vh;
    width: 20vw;
    font-size: 2.5vh;
    position: -webkit-sticky;
    position: sticky;
  }

  .image_delta img {
    display:none;
  }

}


.side-drawer{
	height: 80%;
	background: white;
	
	position: fixed;
	top: 0;
	left: 0;
	width: 50%;
	/*max-width: 400px;*/
	z-index: 200;
	transform: translateY(-100%);
	transition: transform 0.3s ease-out;
	left: 50vw;
	color: #1A1A1A;
}

.side-drawer.open{
	transform: translateY(0);
	left: 50vw;
	font-family: 'Poppins', sans-serif;
	color: #1A1A1A;
	width: 50vw;
	text-align: left;
	text-align: initial;
	font-weight: 500;
	height: 90vh;
}

.side-drawer ul{
	height: 100vh;
	list-style: none;
	display: flex;
	flex-direction: column;
	justify-content: center;
	color: #1A1A1A;
	margin-left: 15%;
	margin-right: 10%;
}

.side-drawer li{
	margin: 0.5rem 0;
}

.side-drawer a{
	text-decoration: none;
	font-size: 2.3vh;
	color: #1A1A1A;
}

.side-drawer a:hover,
.side-drawer a:active{
	color: #032C66;
}

@media (min-width: 769px){
	.side-drawer{
		display: none;
	}
}
.toggle-button{
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	height: 24px;
	width: 30px;
	background: transparent;
	border: none;
	cursor: pointer;
	padding: 0;
	box-sizing: border-box;
	transform: rotate(180deg);

}

.toggle-button:focus{
	outline: none;
}

.toggle-button__line{
	width: 25px;
	height: 2.8px;
	background: #032C66;
	border-radius: 100px;
}

#tg3{
	width: 17px;
}

#tg1{
	width: 17px;
}
.toolbar{
	width: 100%;
	top: 0;
	left: 0;
	position: fixed;
	background: #FFFFFF;
	height: 56px;
	z-index: 300;
	font-family: 'Poppins', sans-serif;
	color: #1A1A1A;
}

.toolbar.active{
	background-color: #C4C4C4;
	color: #EBECEE;
}

.toolbar__navigation{
	display: flex;
	height: 100%;
	align-items: center;
	padding: 0 1rem;
}

.toolbar__logo{
	margin-left: 1rem;
	background-image: url(/static/media/LogoAzul.74df1377.svg);
	width: 63px;
	height: 63px;
	background-position: center;
  background-repeat: no-repeat; 
  background-size: contain;
}

.toolbar__logo.active{

	background-image: url(/static/media/Logo.01026cdf.svg);

}

.toolbar__logo a{
	
	text-decoration: none;
	font-size: 1.3rem;
	font-weight: 400;
}

.spacer{
	flex: 1 1;
}

.toolbar_navigation-items ul{
	list-style: none;
	margin: 0;
	padding: 0;
	display: flex;
}

.toolbar_navigation-items li{
	padding: 0 0.8rem;
}

.toolbar_navigation-items a{
	color: #1A1A1A;
	text-decoration: none;
}

.toolbar_navigation-items.active a{
	color: #EBECEE;
}

.toolbar_navigation-items a:hover,
.toolbar_navigation-items a:active{
	color: #032C66;
}

@media (max-width: 768px){
	.toolbar_navigation-items{
		display: none;
	}
}

@media (min-width: 769px){
	.toolbar__toggle-button{
		display: none;
	}
	.toolbar__logo{
		margin-left: 0;
	}
}
